<template>
  <div
    :class="[
      'relative overflow-hidden bg-gray-100 dark:bg-gray-800',
      sizeClasses,
      roundedClasses,
      'flex items-center justify-center',
    ]"
  >
    <!-- Image -->
    <img
      v-if="src"
      :src="src"
      :alt="alt"
      :class="['h-full w-full object-cover', { 'animate-pulse': loading }]"
      @load="onImageLoad"
      @error="onImageError"
    />

    <!-- Fallback avec initiales -->
    <div
      v-else-if="name && !src"
      :class="[
        'flex items-center justify-center font-semibold text-gray-600 dark:text-gray-300',
        textSizeClasses,
      ]"
    >
      {{ initials }}
    </div>

    <!-- Fallback avec icône -->
    <div
      v-else
      :class="[
        'flex items-center justify-center text-gray-400 dark:text-gray-500',
        iconSizeClasses,
      ]"
    >
      <FontAwesomeIcon icon="user" />
    </div>

    <!-- Indicateur de statut -->
    <div
      v-if="status"
      :class="[
        'absolute rounded-full border-2 border-white dark:border-gray-900',
        statusClasses,
        statusPositionClasses,
      ]"
    ></div>

    <!-- Badge de notification -->
    <div
      v-if="badge"
      :class="[
        'absolute flex h-5 min-w-[20px] items-center justify-center rounded-full bg-red-500 px-1 text-xs font-bold text-white',
        badgePositionClasses,
      ]"
    >
      {{ badge > 99 ? '99+' : badge }}
    </div>
  </div>
</template>

<script setup lang="ts">
import { computed, ref } from 'vue'

export interface AvatarProps {
  src?: string
  alt?: string
  name?: string
  size?: 'xs' | 'sm' | 'md' | 'lg' | 'xl' | '2xl'
  rounded?: 'none' | 'sm' | 'md' | 'lg' | 'full'
  status?: 'online' | 'offline' | 'away' | 'busy'
  badge?: number
  loading?: boolean
}

const props = withDefaults(defineProps<AvatarProps>(), {
  size: 'md',
  rounded: 'full',
  alt: 'Avatar',
  loading: false,
})

const emit = defineEmits<{
  imageLoad: [event: Event]
  imageError: [event: Event]
}>()

const loading = ref(props.loading)

// Classes calculées pour les tailles
const sizeClasses = computed(() => {
  const sizes = {
    xs: 'w-6 h-6',
    sm: 'w-8 h-8',
    md: 'w-10 h-10',
    lg: 'w-12 h-12',
    xl: 'w-16 h-16',
    '2xl': 'w-20 h-20',
  }
  return sizes[props.size]
})

// Classes pour les coins arrondis
const roundedClasses = computed(() => {
  const rounded = {
    none: 'rounded-none',
    sm: 'rounded-sm',
    md: 'rounded-md',
    lg: 'rounded-lg',
    full: 'rounded-full',
  }
  return rounded[props.rounded]
})

// Taille du texte pour les initiales
const textSizeClasses = computed(() => {
  const sizes = {
    xs: 'text-xs',
    sm: 'text-xs',
    md: 'text-sm',
    lg: 'text-base',
    xl: 'text-lg',
    '2xl': 'text-xl',
  }
  return sizes[props.size]
})

// Taille de l'icône de fallback
const iconSizeClasses = computed(() => {
  const sizes = {
    xs: 'text-xs',
    sm: 'text-sm',
    md: 'text-base',
    lg: 'text-lg',
    xl: 'text-xl',
    '2xl': 'text-2xl',
  }
  return sizes[props.size]
})

// Initiales à partir du nom
const initials = computed(() => {
  if (!props.name) return ''

  return props.name
    .split(' ')
    .map((word) => word.charAt(0).toUpperCase())
    .slice(0, 2)
    .join('')
})

// Classes pour le statut
const statusClasses = computed(() => {
  if (!props.status) return ''

  const statusStyles = {
    online: 'bg-green-400',
    offline: 'bg-gray-400',
    away: 'bg-yellow-400',
    busy: 'bg-red-400',
  }
  return statusStyles[props.status]
})

// Position du statut
const statusPositionClasses = computed(() => {
  const positions = {
    xs: 'w-2 h-2 -bottom-0 -right-0',
    sm: 'w-2 h-2 -bottom-0 -right-0',
    md: 'w-3 h-3 -bottom-0.5 -right-0.5',
    lg: 'w-3 h-3 -bottom-0.5 -right-0.5',
    xl: 'w-4 h-4 -bottom-1 -right-1',
    '2xl': 'w-4 h-4 -bottom-1 -right-1',
  }
  return positions[props.size]
})

// Position du badge
const badgePositionClasses = computed(() => {
  const positions = {
    xs: '-top-1 -right-1',
    sm: '-top-1 -right-1',
    md: '-top-2 -right-2',
    lg: '-top-2 -right-2',
    xl: '-top-2 -right-2',
    '2xl': '-top-3 -right-3',
  }
  return positions[props.size]
})

// Gestionnaires d'événements
const onImageLoad = (event: Event) => {
  loading.value = false
  emit('imageLoad', event)
}

const onImageError = (event: Event) => {
  loading.value = false
  emit('imageError', event)
}
</script>
