<script setup lang="ts">
import { computed } from 'vue'
// Compute the correct main image URL for dev/prod
const baseImageUrl = computed(() => {
  if (!props.project?.image) return ''
  const img = props.project.image.startsWith('/') ? props.project.image.slice(1) : props.project.image
  return import.meta.env.BASE_URL + img
})

// Compute the correct gallery image URL for dev/prod
function getGalleryImageUrl(image: string | null): string {
  if (!image) return ''
  const img = image.startsWith('/') ? image.slice(1) : image
  return import.meta.env.BASE_URL + img
}
import { watch, onUnmounted, ref } from 'vue'
import { useI18n } from '@/composables/useI18n'

interface Project {
  id: string
  title: string
  description: string
  longDescription: string
  image: string
  images?: string[]
  technologies: string[]
  category: string
  status: string
  year: string
  duration: string
  team: string
  role: string
  challenges?: string[]
  solutions?: string[]
  results?: string[]
  links: {
    demo?: string
    github?: string
    case_study?: string
    figma?: string
  }
  metrics?: {
    performance: number
    accessibility: number
    seo: number
    best_practices: number
  }
}

interface Props {
  isOpen: boolean
  project: Project | null
}

interface Emits {
  (e: 'close'): void
}

const props = defineProps<Props>()
const emit = defineEmits<Emits>()

const { tUI } = useI18n()

const isImageModalOpen = ref(false)
const selectedImage = ref<string | null>(null)

// Nettoyer au démontage du composant
onUnmounted(() => {
  document.body.style.overflow = ''
  document.removeEventListener('keydown', handleKeyDown)
})

// Gestion de la touche Escape pour fermer le modal
const handleKeyDown = (event: KeyboardEvent) => {
  if (event.key === 'Escape') {
    if (isImageModalOpen.value) {
      closeImageModal()
      return
    }

    if (props.isOpen) {
      closeModal()
    }
  }
}

// Ajouter/supprimer l'event listener selon l'état du modal
watch(
  () => props.isOpen,
  (isOpen) => {
    if (isOpen) {
      document.body.style.overflow = 'hidden'
      document.addEventListener('keydown', handleKeyDown)
    } else {
      document.body.style.overflow = ''
      document.removeEventListener('keydown', handleKeyDown)
    }
  }
)

const closeModal = () => {
  if (isImageModalOpen.value) {
    closeImageModal()
  }
  emit('close')
}

const closeImageModal = () => {
  isImageModalOpen.value = false
  selectedImage.value = null
}

const getStatusClass = (status: string) => {
  switch (status.toLowerCase()) {
    case 'terminé':
    case 'completed':
      return 'bg-green-500/20 text-green-300'
    case 'en développement':
    case 'in development':
      return 'bg-yellow-500/20 text-yellow-300'
    case 'en cours':
    case 'in progress':
      return 'bg-blue-500/20 text-blue-300'
    default:
      return 'bg-gray-500/20 text-gray-300'
  }
}

const openImageModal = (image: string) => {
  selectedImage.value = image
  isImageModalOpen.value = true
}
</script>

